.ciq-asset-information{background:transparent;display:flex;height:14px;justify-content:flex-start;left:10px;margin:15px 0;opacity:1;position:relative;top:10px;transition:all .1s;z-index:1}.ciq-asset-information.hide{height:0;margin:0;opacity:0;pointer-events:none}.ciq-asset-information>div{align-items:center;display:flex;font-size:12px;justify-content:flex-start;padding-right:5px}.ciq-asset-information>div div:first-child{padding:0 8px}.ciq-asset-information>div div:last-child{font-weight:700}.smartcharts-light .ciq-asset-information>div{color:#333}.smartcharts-dark .ciq-asset-information>div{color:#aaacba}.smartcharts-mobile .ciq-asset-information{flex-wrap:wrap;left:2px}.smartcharts-mobile .ciq-asset-information>div{font-size:12px}.smartcharts-mobile .ciq-asset-information>div div:first-child{padding:0 5px}