.cq-dialog{border-radius:5px;box-shadow:0 0 .63em .03em rgba(0,0,0,.14);left:calc(50% - 172px);margin-top:120px;opacity:0;position:absolute;top:auto;transition:pointer-events .2s cubic-bezier(.64,.04,.35,1),transform .25s cubic-bezier(.64,.04,.35,1),opacity .17s cubic-bezier(.64,.04,.35,1),z-index .17s cubic-bezier(.64,.04,.35,1);visibility:hidden;width:344px;z-index:-3}.smartcharts-light .cq-dialog{background:#fff}.smartcharts-dark .cq-dialog{background:#2a3052}.cq-dialog.open{margin-top:0;opacity:1;visibility:visible;z-index:3}.smartcharts-mobile .cq-dialog{left:10px;width:calc(100vw - 20px)}