.cq-crosshair{left:0;opacity:0;position:relative;top:0;z-index:30}.cq-crosshair .cq-crosshair-content{-moz-box-sizing:border-box;border-radius:4px;box-shadow:0 2px 8px 0 rgba(21,33,45,.32);box-sizing:border-box;display:flex;flex-direction:column;padding:8px;pointer-events:none;position:absolute}.smartcharts-light .cq-crosshair .cq-crosshair-content{background:rgba(21,33,45,.88);color:#fff}.smartcharts-dark .cq-crosshair .cq-crosshair-content{background:hsla(0,0%,100%,.88);color:#2a3052}.cq-crosshair .cq-crosshair-content:after,.cq-crosshair .cq-crosshair-content:before{border-bottom:4px solid transparent;border-top:4px solid transparent;content:"";display:none;height:0;position:absolute;top:calc(50% - 4px);width:0}.cq-crosshair .cq-crosshair-content:after{left:-6px}.smartcharts-light .cq-crosshair .cq-crosshair-content:after{border-right:6px solid rgba(21,33,45,.88)}.smartcharts-dark .cq-crosshair .cq-crosshair-content:after{border-right:6px solid hsla(0,0%,100%,.88)}.cq-crosshair .cq-crosshair-content:before{left:100%}.smartcharts-light .cq-crosshair .cq-crosshair-content:before{border-left:6px solid rgba(21,33,45,.88)}.smartcharts-dark .cq-crosshair .cq-crosshair-content:before{border-left:6px solid hsla(0,0%,100%,.88)}.cq-crosshair .cq-crosshair-content.arrow-left{transform:translateX(30px) translateY(-50%)}.cq-crosshair .cq-crosshair-content.arrow-right{transform:translateX(calc(-100% - 30px)) translateY(-50%)}.cq-crosshair .cq-crosshair-content.arrow-left:after,.cq-crosshair .cq-crosshair-content.arrow-right:before{display:block}.cq-crosshair .cq-crosshair-content .row{font-size:12px;line-height:18px}.cq-crosshair .cq-crosshair-content .row span:first-child{padding-right:5px}.cq-crosshair .cq-crosshair-content .row span:last-child{font-weight:700}.cq-crosshair.active{opacity:1;pointer-events:auto}