.ciq-list{max-height:260px;overflow-y:auto;position:relative}.ciq-list.ps{padding-right:11px}.ciq-list.scrollbar-container .ciq-list-item:first-child{margin-top:16px}.ciq-list.scrollbar-container .ciq-list-item:nth-last-child(-n+3){margin-bottom:16px}.ciq-list .ciq-list-item{-moz-box-sizing:border-box;align-items:center;border-bottom:none;box-sizing:border-box;cursor:pointer;display:flex;font-size:12px;height:32px;justify-content:flex-start;line-height:32px;padding:0 0 0 16px}.smartcharts-light .ciq-list .ciq-list-item{background:#fff;border-bottom:1px solid #e9e9ed;border-left:1px solid #e9e9ed;border-right:1px solid #e9e9ed;color:#333}.smartcharts-dark .ciq-list .ciq-list-item{background:#2a3052;border-bottom:1px solid rgba(170,172,186,.3);border-left:1px solid rgba(170,172,186,.3);border-right:1px solid rgba(170,172,186,.3);color:#aaacba}.smartcharts-light .ciq-list .ciq-list-item:first-child{border-top:1px solid #e9e9ed}.smartcharts-dark .ciq-list .ciq-list-item:first-child{border-top:1px solid rgba(170,172,186,.3)}.ciq-list .ciq-list-item.active{font-weight:700}.smartcharts-light .ciq-list .ciq-list-item.active{background-color:#f4f4f6;color:#2a3052}.smartcharts-dark .ciq-list .ciq-list-item.active{background-color:#3f4463;color:#fff}.ciq-list .ciq-list-item.disabled{pointer-events:none}.ciq-list .ciq-list-item.disabled>*{cursor:auto;opacity:.2}